@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&family=Staatliches&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-size: 30px;
}

body, ul, p {
  margin: 0;
  padding: 0;
}

.App {
  color: #ccc;
  font-family: 'Shizuru', cursive;
  background-color: rgba(51, 51, 51, 0.966);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.App__title {
  font-family: 'Shadows Into Light', cursive;
  text-align: center;
}

.App__subtitle {
  font-size: 1.5em;
  font-style: italic;
}

.App__btn {
  /* background-color: #bbb;
   */
  width: 100px;
  font-size: 0.5rem;
  background-color: rgba(51, 51, 51, 0.966);
  font-family: inherit;
  color: white;
  margin-left: 10px;
  padding: 10px 15px;
  border-radius: 15px;
}

.btn-on {
  color: green;
  box-shadow: -2px -2px 5px #222;
}

.btn-off {
  color: palevioletred;
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
}

.App__btn:hover {
  text-shadow: 2px 2px 4px black;
}


.App__sizeFild {
  width: 380px;
  position: absolute;
  right: 0;
  top: 74%;
  -webkit-transform:rotate(90deg);
          transform:rotate(90deg);
  -webkit-transform-origin: top right;
          transform-origin: top right;

}

.App__control {
  position: relative;
  height: -webkit-max-content;
  height: max-content;
  width: -webkit-max-content;
  width: max-content;
}


.GoodsList{list-style:none;display:flex;flex-direction:column;margin-bottom:20px;overflow:hidden}.GoodsList__item{height:30px;width:-webkit-max-content;width:max-content;line-height:30px;transition:-webkit-transform 0.5s;transition:transform 0.5s;transition:transform 0.5s, -webkit-transform 0.5s}.GoodsList__item:not(:last-child){margin-bottom:8px}.GoodsList__item:hover{-webkit-transform:translateX(5px);transform:translateX(5px);text-shadow:2px 2px 4px greynp}

